            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
            },

            /**
             * 
             * divStr  需要滚动条的盒子
             * niceScroll(".el-table__body",{})  需要滚动的内容盒子---滚动条盒子的子元素 {} 修改滚动条的参数详情百度搜索niceScroll
             * cursorcolor    滚动条颜色
             * cursorwidth    滚动条宽度
             * autohidemode   滚动条是否隐藏
             * background     滚动条背景颜色
             * preventmultitouchscrolling    防止多触点事件引发滚动
             */
            setScrollbar(divStr){
                $(divStr).niceScroll(".el-table__body",{
                    cursorcolor: "#2f78ef",
                    cursorwidth: "5px",
                    autohidemode: false,
                    background: "#e6e6e6",
                    // nativeparentscrolling: false,
                    preventmultitouchscrolling: true,
                    bouncescroll: false
                    // railoffset: true
                })
            }
        },
        mounted(){
            // console.log($('.el-table__body-wrapper'));
            // 设置登陆日志表格的滚动条样式
            this.setScrollbar('.c_tab_logon .el-table__body-wrapper')
